import React from 'react'
import styled from 'styled-components'
import { Flex, Heading, ImageCustom } from '@pancakeswap-libs/uikit'

export interface ExpandableSectionProps {
  image?: string
  title?: string
  unit?: string
}

const Wrapper = styled(Flex)`
  svg {
    margin-right: 0.25rem;
  }
`

const ContainerImage = styled.div`
  position: relative;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 250px;
  max-width: 250px;
  max-height: 250px;
  width: 100%;
  padding-top: 0%;
`

const Image = styled.img`
  position: absolute;
  top: 0px;
  left: 0px;
  max-width: 100%;
  border-radius: 7px;
`

const CardHeading: React.FC<ExpandableSectionProps> = ({
  image,
  title,
  unit
}) => {
  return (
    <div>
      <Wrapper justifyContent="center" alignItems="center" mb="12px">
        {/* <ImageCustom src={image} alt={image} width={250} height={250} /> */}

        <ContainerImage>
          <Image 
            src={image} 
            alt={image} 
          />
        </ContainerImage>
      </Wrapper>
      <Heading mb="5px">{title} <span style={{ fontWeight: 'normal', fontSize: 14 }}> &nbsp; #{unit}</span></Heading>
    </div>
  )
}

export default CardHeading
