import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import styled from 'styled-components'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { provider } from 'web3-core'
import { Heading, Spinner } from '@pancakeswap-libs/uikit'
import FlexLayout from 'components/layout/Flex'
import Page from 'components/layout/Page'
import useRefresh from 'hooks/useRefresh'
import ItemCard from './components/ItemCard/ItemCard'
import { getItemsSell } from '../../utils/callHelpers'
import { useNFT } from '../../hooks/useContract'

export interface FarmsProps{
  tokenMode?: boolean
}

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 70px;

  @media (max-width: 768px) {
    margin-left: 0px;
    margin-right: 60px;
  }
`

const Home: React.FC<FarmsProps> = (farmsProps) => {

  const { account, ethereum }: { account: string; ethereum: provider } = useWallet()
  const contractNFT = useNFT();

  const [data, setData] = useState([]);


  const dispatch = useDispatch()
  const { fastRefresh } = useRefresh()
  useEffect(() => {
    async function getData(){
      const x = await getItemsSell(contractNFT);
      setData(x)
    }
    getData()
  }, [account, dispatch, fastRefresh, contractNFT])


  return (
    <Page>
      <Heading as="h1" size="lg" color="primary" mb="70px" style={{ textAlign: 'center', fontWeight: 'normal' }}>
        Create, sell, buy and resell NFTs in a simple, fast and easy platform
      </Heading>

      <Heading as="h2" size="md" color="" mb="30px" style={{ textAlign: 'center' }}>
        Collect your NFT now!
      </Heading>


      <div>
        {/* <Divider /> */}
        <FlexLayout>
          {data.length ? 
            data.map(item => ( 
              <ItemCard
                key={item.id}
                ethereum={ethereum}
                account={account}
                item={item}
                contract={contractNFT}
              />
            ))
          : 
          
          <Wrapper>
            <Spinner />
          </Wrapper>
            }
         
        </FlexLayout>
      </div>
      {/* <Image src="/images/egg/mill.gif" alt="illustration" width={600} height={400} responsive /> */}
    </Page>
  )
}

export default Home
